export default {
	data() {
		return {
			books: []
		}
	},







	onShow() {
		this.query();
	},
	methods: {


















		query() {
			this.$api.queryBook().then(res => {
				if (res.data.code === 2000) {
					this.books = res.data.data;
				} else if (res.data.code === 5003) {
					uni.showToast({
						title: '登陆过期，请重新登录',
						icon: 'none'
					})
				} else {
					uni.showToast({
						title: '系统维护中...',
						icon: 'none'
					})
				}
			})
		},
		addShopCart(id) {
			this.$api.addShopCart(this.books.filter(book => book.id == id)[0]).then(res => {
				if (res.data.code === 2000) {
					this.query();
				} else if (res.data.code === 5003) {
					uni.showToast({
						title: '登陆过期，请重新登录',
						icon: 'none'
					})
				} else {
					uni.showToast({
						title: '操作失败，系统维护中...',
						icon: 'none'
					})
				}
			})
		},
		removeShopCart(id) {
			this.$api.removeShopCart(id).then(res => {
				if (res.data.code === 2000) {
					this.query();
				} else if (res.data.code === 5003) {
					uni.showToast({
						title: '登陆过期，请重新登录',
						icon: 'none'
					})
				} else {
					uni.showToast({
						title: '操作失败，系统维护中...',
						icon: 'none'
					})
				}
			})
		}
	}
}
